import { SET_DEPARTAMENTS_DATA } from "../utilities/const"

let initialState = {
    departamentsData: [
        {
            id: 1,
            name: 'Ангарский район водных путей и судоходства (АРВПиС)',
            address: '',
            path: '/arvps'
        },
        {
            id: 2,
            name: 'Байкало-Селенгинский район водных путей и судоходства (БСРВПиС)',
            address: '',
            path: '/bsrvps'
        }
    ],
}

const departamentsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_DEPARTAMENTS_DATA:
            return {
                ...state,
                newsData: action.newsData
            }
        default:
            return state
    }
}

const setDepartaments = (newsData) => {
    return { type: SET_DEPARTAMENTS_DATA, newsData: newsData }
}

export const getDepartaments = () => {
    return (setDepartaments(initialState))
}

export default departamentsReducer