import React from 'react'
import { NavLink } from 'react-router-dom'

import s from './Navbar.module.css'

const Navbar = (props) => {
  console.log(props)
  return (
        <nav className={s.nav}>
            {props.departamentsData.map(depatamentData => {
                        return <div className={s.item} key = {depatamentData.id}>
                        <NavLink to={depatamentData.path}
                            activeClassName={s.activeLink}>
                            {depatamentData.name}
                        </NavLink>
                    </div>
                    })}
        </nav>
    )
}

export default Navbar
